<template>
  <div>
    <div class="text-h5 mb-10">Merchant List</div>

    <v-card class="table">
      <v-card-title class="table_head">
        <div class="table_title mb-2">
          <v-select
            class="table_select"
            v-model="sortBy"
            flat
            solo-inverted
            hide-details
            dense
            color="white"
            :items="keys"
            label="Search term"
          ></v-select>
          <v-text-field
            v-model="search"
            label="Search"
            hide-details
            solo-inverted
            flat
            dense
            class="table_search"
          ></v-text-field>
          <div class="table_box table_choise">
            Member level:
            <v-checkbox
              label="Beginner"
              color="primary"
              value=""
              hide-details
              dense
              class="mr-1 ml-1"
            ></v-checkbox>
            <v-checkbox
              label="Advancer"
              color="primary"
              value=""
              hide-details
              dense
              class="mr-1"
            ></v-checkbox>
            <v-checkbox
              label="Expert"
              color="primary"
              value=""
              hide-details
              dense
              class="mr-1"
            ></v-checkbox>
            <v-checkbox
              label="Master"
              color="primary"
              value=""
              hide-details
              dense
              class="mr-1"
            ></v-checkbox>
          </div>
        </div>
        <div class="table_title mb-1 justify-center">
          <div class="table_box">
            <v-btn color="blue darken-3" class="mr-3 white--text">
              Search
            </v-btn>
            <v-btn
              color="blue darken-3"
              class="white--text"
              @click="resetInput()"
            >
              Reset
            </v-btn>
          </div>
        </div>
        <div class="table_title">
          <v-spacer></v-spacer>
          <div class="table_box table_box_checkbox">
            <v-checkbox
              label="Apply current search filter"
              color="primary"
              hide-details
              dense
              class="mr-2 table_checkbox_current"
            ></v-checkbox>
            <v-btn @click="showDialogExcel()" x-small>
              Excel file Download
            </v-btn>
          </div>
        </div>
      </v-card-title>
      <v-data-table
        :headers="headers"
        :items="$store.state.merchantList"
        :search="search"
        class="table_data"
      >
        <template v-slot:top>
          <v-toolbar flat>
            <v-toolbar-title class="text-h6" style="font-weight: 700">
              Total
              <span style="color: #0d47a1">{{
                $store.state.merchantList.length
              }}</span>
            </v-toolbar-title>
          </v-toolbar>

          <v-dialog v-model="dialogExcel" max-width="400px">
            <v-card>
              <v-container>
                <div
                  class="card_title text-center mb-8"
                  v-text="titleDialogExcel"
                ></div>
              </v-container>

              <v-card-actions class="justify-center">
                <v-btn color="primary" @click="closeDialogExcel()">
                  Confirm
                </v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </template>
        <template v-slot:item.checkbox="{ item }">
          <v-checkbox
            color="blue darken-3"
            v-model="item.checkbox"
            style="width: fit-content"
          ></v-checkbox>
        </template>
      </v-data-table>
    </v-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      search: "",
      sortBy: "",
      keys: ["All", "Active", "Sespended", "Secession"],
      headers: [
        { text: "Merchant", align: "start", value: "merchant" },
        { text: "Merchant level", value: "level" },
        { text: "Total Profit (%)", value: "total" },
        { text: "Holding Profit", value: "profit" },
        { text: "Upload Pattern", value: "upload" },
        { text: "Downloads", value: "downloads" },
        { text: "Suspend Uploader", value: "checkbox" },
      ],
      titleDialogExcel: "",
      dialogExcel: false,
    };
  },

  methods: {
    resetInput() {
      let inputCheckbox = document.querySelectorAll(
        ".table_head .v-input--selection-controls__input input"
      );

      inputCheckbox.forEach((element) => {
        if (element.checked) {
          element.click();
        }
      });

      this.search = "";
      this.sortBy = "";
    },
    showDialogExcel() {
      let isChecked = document.querySelector(".table_checkbox_current input");
      if (isChecked.checked) {
        this.titleDialogExcel =
          "Do you want to get the current search results in Excel?";
      } else {
        this.titleDialogExcel =
          "Do you want to get the entire data into Excel?";
      }
      this.dialogExcel = true;
    },
    closeDialogExcel() {
      this.dialogExcel = false;
    },
  },
};
</script>
